	module.exports = {"data":{"markdownRemark":{"html":"<h2>Blockchain</h2>\n<blockquote>\n<p>“Is a decentralized database that stores a registry of assets and transactions across a peer-to-peer network. Is basically a public registry of who owns what and who transacts what. The transactions are secured by cryptography, and over time, that transaction history gets locked in blocks of data that are then cryptographically linked together and secured. This creates an immutable, unforgeable record of all the transactions across this network. This record is replicated on every computer that uses the network.”</p>\n<p>—Bettina Warburg<sup>1</sup></p>\n</blockquote>\n<p>Blockchain is often associated with Bitcoin and other cryptocurrencies, but they are not the same thing. Blockchain is the structure where Bitcoin runs on, and besides cryptocurrencies, it can be applied in many other fields like digital identity, supply chain and even <a href=\"https://www.democracy.earth/\">democracy</a>.</p>\n<p>Even though blockchain can be applied to a big range of problems, is not the solution to everything. This technology is usually used  to solve problems where the parts don’t trust each other.</p>\n<p>A third party is usually needed to validade trust relationships. In the most common example, which is the banking system, a trusted authority is needed, such as a bank, to intermediate transactions, manage accounts, verify balances, and validate transfers. Blockchain then, comes in this case to replace the regulatory authority. In the case of cryptocurrencies, it replaces the figure of the bank, doing all the validations and guaranteeing the security and veracity in the transactions.</p>\n<p>For this reason Blockchain is also known as <strong>“The Trust Protocol”</strong>.</p>\n<h3>Sources</h3>\n<ol>\n<li><a href=\"https://youtu.be/RplnSVTzvnU?t=213\">Bettina Warburg, “How the blockchain will radically transform the economy”, <em>TED Talks</em>, December 8, 2016</a></li>\n</ol>\n<h4>More information</h4>\n<p><a href=\"https://hackernoon.com/wtf-is-the-blockchain-1da89ba19348\">WTF is The Blockchain?</a><br>\n<a href=\"https://medium.freecodecamp.org/blockchain-is-our-first-22nd-century-technology-d4ad45fca2ce\">Blockchain: the revolution we’re not ready for</a><br>\n<a href=\"https://www.youtube.com/watch?v=Pl8OlkkwRpc\">How the blockchain is changing money and business | Don Tapscott (YouTube video)</a></p>","fields":{"slug":"/blockchain/"},"frontmatter":{"title":"Blockchain"}}},"pathContext":{"slug":"/blockchain/"}}

